'use strict';

var List = require("bs-platform/lib/js/list.js");
var Hashtbl = require("bs-platform/lib/js/hashtbl.js");
var Caml_obj = require("bs-platform/lib/js/caml_obj.js");
var Caml_array = require("bs-platform/lib/js/caml_array.js");
var Caml_int32 = require("bs-platform/lib/js/caml_int32.js");
var Pervasives = require("bs-platform/lib/js/pervasives.js");
var PriorityQueue = require("bs-priority-queue/src/PriorityQueue.bs.js");
var Unit$ReasonReactStarter = require("./Unit.bs.js");
var SharedType$ReasonReactStarter = require("./SharedType.bs.js");

function aStar(grid, startCoord, endCoord) {
  var rowLen = grid.length;
  var colLen = Caml_array.caml_array_get(grid, 0).length;
  var getEuclidean = function (coordinate) {
    return Pervasives.abs(endCoord.row - coordinate.row | 0) + Pervasives.abs(endCoord.col - coordinate.col | 0) | 0;
  };
  var getNeighbors = function (coordinate, currCost, visited) {
    var right_row = coordinate.row;
    var right_col = coordinate.col + 1 | 0;
    var right = {
      row: right_row,
      col: right_col
    };
    var bottom_row = coordinate.row - 1 | 0;
    var bottom_col = coordinate.col;
    var bottom = {
      row: bottom_row,
      col: bottom_col
    };
    var left_row = coordinate.row;
    var left_col = coordinate.col - 1 | 0;
    var left = {
      row: left_row,
      col: left_col
    };
    var upper_row = coordinate.row + 1 | 0;
    var upper_col = coordinate.col;
    var upper = {
      row: upper_row,
      col: upper_col
    };
    return List.filter((function (neiCoord) {
                    if (neiCoord.row >= 0 && neiCoord.row < rowLen && neiCoord.col >= 0 && neiCoord.col < colLen && !Unit$ReasonReactStarter.isWater(Caml_array.caml_array_get(Caml_array.caml_array_get(grid, neiCoord.row), neiCoord.col))) {
                      if (Hashtbl.mem(visited, SharedType$ReasonReactStarter.coordStr(neiCoord))) {
                        return (currCost + Unit$ReasonReactStarter.unitCost(Caml_array.caml_array_get(Caml_array.caml_array_get(grid, neiCoord.row), neiCoord.col)) | 0) < Hashtbl.find(visited, SharedType$ReasonReactStarter.coordStr(neiCoord));
                      } else {
                        return true;
                      }
                    } else {
                      return false;
                    }
                  }))(/* :: */[
                right,
                /* :: */[
                  bottom,
                  /* :: */[
                    left,
                    /* :: */[
                      upper,
                      /* [] */0
                    ]
                  ]
                ]
              ]);
  };
  var minQueue = PriorityQueue.make((function (nodeA, nodeB) {
          return nodeA.heuristic - nodeB.heuristic | 0;
        }));
  minQueue.push({
        heuristic: getEuclidean(startCoord),
        cost: 0,
        currCoord: startCoord,
        prevNode: undefined
      });
  var visited = Hashtbl.create(true, Caml_int32.imul(colLen, rowLen));
  Hashtbl.add(visited, SharedType$ReasonReactStarter.coordStr(startCoord), 0);
  var aStarRec = function (pq, visited) {
    while(true) {
      var minNode = PriorityQueue.pop(pq);
      if (minNode === undefined) {
        return /* Unreacheable */0;
      }
      if (Caml_obj.caml_equal(minNode.currCoord, endCoord)) {
        return /* Last */[minNode];
      }
      var currCost = minNode.cost;
      var neighbors = getNeighbors(minNode.currCoord, currCost, visited);
      List.map((function(currCost){
          return function (n) {
            return Hashtbl.replace(visited, SharedType$ReasonReactStarter.coordStr(n), currCost + Unit$ReasonReactStarter.unitCost(Caml_array.caml_array_get(Caml_array.caml_array_get(grid, n.row), n.col)) | 0);
          }
          }(currCost)), neighbors);
      var neighborNodes = List.map((function(minNode,currCost){
          return function (n) {
            return {
                    heuristic: (currCost + Unit$ReasonReactStarter.unitCost(Caml_array.caml_array_get(Caml_array.caml_array_get(grid, n.row), n.col)) | 0) + getEuclidean(n) | 0,
                    cost: currCost + Unit$ReasonReactStarter.unitCost(Caml_array.caml_array_get(Caml_array.caml_array_get(grid, n.row), n.col)) | 0,
                    currCoord: n,
                    prevNode: minNode
                  };
          }
          }(minNode,currCost)), neighbors);
      List.map((function (nn) {
              pq.push(nn);
              
            }), neighborNodes);
      continue ;
    };
  };
  var lastNode = aStarRec(minQueue, visited);
  var pathConstruct = function (n) {
    var prevNode = n.prevNode;
    if (prevNode !== undefined) {
      return /* :: */[
              n.currCoord,
              pathConstruct(prevNode)
            ];
    } else {
      return /* :: */[
              n.currCoord,
              /* [] */0
            ];
    }
  };
  if (lastNode) {
    return pathConstruct(lastNode[0]);
  } else {
    return /* [] */0;
  }
}

exports.aStar = aStar;
/* PriorityQueue Not a pure module */
